package adventOfCode.year2022

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO

object Day01 extends AocDay {
  val day = 1

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val result = parseReindeerCalories(input).max
    result.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val result = parseReindeerCalories(input).sorted.reverse.take(3).sum
    result.toString
  }

  def parseReindeerCalories(input: String): Array[Int] =
    input.split("\n\n").map { block =>
      block.linesIterator.map(_.toInt).toList.sum
    }
}

